<template>
    <div class="new-product">
        <el-page-header @back="$router.push('/products')" title="back to all products" content="New product"></el-page-header>
        <h1 class="title title--primary">Launch new product</h1>
        <el-form label-position="top">
            <el-form-item label="Category">
                <el-select v-model="selectedCategory" placeholder="Select categories">
                    <el-option
                    v-for="category in categories"
                    :key="category.id"
                    :label="category.name"
                    :value="category.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="Product name">
                <el-input v-model="name" placeholder="Choose a name" @input="autoCreateSlug($event)" />
            </el-form-item>
            <el-form-item label="Url reference">
                <el-input v-model="slug" placeholder="Choose a url reference" @input="$event.length > 0 ? slugChanged = true : slugChanged = false" />
            </el-form-item>
            <el-form-item label="Product description">
                <el-input type="textarea" v-model="description" placeholder="Write a detailed description" @input="autoCreateSlug($event)" />
            </el-form-item>
            <form action="/api/images" method="post" enctype="multipart/form-data">
                <input type="file" name="file">
                <button type="submit" value="Upload">Upload</button>
            </form>
            <el-form-item label="Product images">
                <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="4" v-for="(image, index) in images" :key="index">
                    <img src="image.src">
                </el-col>
                <el-upload
                class="image-upload"
                drag
                action="/api/images"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                :http-request="beforeUpload"
                multiple
                :limit="3"
                :on-exceed="handleExceed"
                :file-list="fileList"
                list-type="picture">
                    <el-button size="small" type="primary">Click to upload</el-button>
                    <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 500kb</div>
                </el-upload>
            </el-form-item>
            <h3>Pricing and fulfillment</h3>
            <el-form-item label="Price">
                £ <el-input-number :controls="false" v-model="price" :precision="2" />
            </el-form-item>
            <el-form-item label="Dimensions (in centimetres)">
                <el-input-number :controls="false" v-model="height" :step="1" /> &nbspx&nbsp 
                <el-input-number :controls="false" v-model="width" :step="1" /> &nbspx&nbsp 
                <el-input-number :controls="false" v-model="length" :step="1" />
            </el-form-item>
            <el-form-item label="Weight">
                <el-input-number :controls="false" v-model="weight" :precision="2" />
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "new-product",
        data() {
            return {
                selectedCategory: null,
                name: null,
                description: null,
                slug: null,
                price: null,
                slugChanged: false,
                images: [],
                categories: [
                    {
                        name: "Tote bags",
                        slug: "tote-bags",
                        description: "we make intriguing bags for intriguing people, and we make them to last",
                        images: [],
                        id: "57329852359235"
                    },
                    {
                        name: "Handbags",
                        slug: "handbags",
                        description: "A classic now, as they always have been and probably always will be",
                        images: [],
                        id: "264235235"
                    }
                ]
            }
        },
        methods: {
            autoCreateSlug(name) {
                if(!this.slugChanged) {
                    this.slug = name.replace(/ /g, "-")
                }
            },
            handleExceed(files, fileList) {
                this.$message.warning(`The limit is 3, you selected ${files.length} files this time, add up to ${files.length + fileList.length} totally`);
            },
            beforeUpload(form) {
                console.log(form)
                this.$axios.post(form.action,
                form.file)
                .then((res) => {
                    console.log(res)
                })
            }
        }
    }
</script>